/* !Basics */

*{
  box-sizing: border-box;
  margin: 0;
}
body {
    font-family: 'Major Mono Display', monospace;
    /* font-size: 1em; */
    font-weight: bolder;
    background-image: url(../public/nasaCluster.jpg);
    background-size: 36vmax;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /* padding: 0.4em; */
  }
  .container{
    max-width: 90%;
    margin-right: auto;
    margin-left: auto;
    padding: 1rem;

  }

  .flex-grid{
    display: flex;
    margin: 1em;
    padding: 0.4em;
  }
  .flex-grid .col {
    flex: 1;
  }


  /* !navbar styling */
.nav {
    height: 50px;
    /* height: 1rem; */
    width: 100%;
    display: flex;
    align-items: center;
    position: relative;
    padding: 0.5rem 0rem;
    color: #F8F8FF;
/* https://blog.logrocket.com/create-responsive-navbar-react-css/ */
    box-shadow: 0 2px 2px 2px rgba(9,9,9,0.2);
    font-size: 1.4em;
    list-style: none;
    background-color: #30504079;
    /* opacity: 40%; */
  }

  #ie4 ul{
    margin-top: auto;
    display: flex;
    gap:1rem;
    margin-left: auto;
    /* font: 1.11em; */
    padding: 0;
  }
  #ie4 li{
    list-style-type: disc;
    margin: 0 1rem;
  }

  #ie4 li a{
    text-decoration: none;
    /* margin: 0.5rem; */
    display: block;
    color:#ff8400;
    width: 100%;
    /* margin-left: auto; */
    padding: 0.4em;
  }

  .buttonSvg {

    border: 0;
    height: 44px;
    width: 44px;
    padding: 0.5rem;
    border-radius: 50%;
    background-color: #5386fd;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
    position: absolute;
    top: 15%;
    left: 80%;
    display: none;
    /* transform: translateY(10%); */
  }
  .buttonSvg:hover {
    background-color: #ff8400;
  }
  #landingPage{
    color: #ff8400;
    background-color: #30504079;
    /* padding: 0 2em; */
  }
  #revealer{
    /* font-weight: 1444; */
    /* font-family: 'Major Mono Display', monospace; */
    /* align-items: center; */
    color: #ff8400;
    background-color: #30504079;
  }

  input{
    color: #ff8400;
    margin: 0.3em;
    /* opacity: 10%; */
    /* margin-top: 21px; */
  }


  #pictureRef {
    font-size: 0.8em;
    background-color: #305040af;
    /* background-color: rgba(245, 222, 179, 0.887); */
    /* opacity: 77%; */
  }
  #pictureRef a {
  color: #00ff80;
  }


  @media screen and (max-width: 1000px) {
    #ie4 ul{
    font-size: 0.8em;
    }
    .container{
      font-size: 0.8rem;
    }
  }
  @media screen and (max-width: 768px) {
    .buttonSvg {
      display: block;
    }
    #ie4 ul{
      display: none;

    }
  }